export default {
	components: {
		headers
	},
	data() {
		return {
			Htitle: "领用信用卡",
			Hlicon: "../../static/icon/left_whitch.png",
			// hstyle:"background:transparent"
			data:''
		}
	},
	methods:{
		navUrl(url){
			uni.setStorageSync('webUrl' , url);
			setTimeout(()=>{
				uni.navigateTo({
					url:"/pages/index/webview"
				});
			},1500);
			console.log(url)
		}
	},
	onLoad() {
		service.auth(this , service.api.center.financialCard , 'GET' , {} , function(self ,res){
			console.log(res);
			self.data = res.data.data;
		});
		
	}
}
